@import "https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;700&display=swap";
html {
  box-sizing: border-box;
  scroll-behavior: smooth;
  width: 100%;
  height: 100%;
  font-size: 100%;
  line-height: 1.15;
}

body {
  color: #000;
  background-color: #edf1ff;
  margin: 0;
  padding: 0;
  font-family: Inter, Arial, Helvetica, sans-serif;
  font-style: normal;
  font-weight: 400;
  line-height: 1.15;
  text-decoration: none;
}

* {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

button, select {
  text-transform: none;
  cursor: pointer;
  border: none;
}

a {
  color: inherit;
  text-decoration: none;
}

input {
  padding: 2px 4px;
}

img {
  display: block;
}

ul > li {
  list-style: none;
}

.cangra-nav {
  width: 100%;
  z-index: 1;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 16px 64px;
  display: flex;
  position: fixed;
}

@media only screen and (max-width: 767px) {
  .cangra-nav {
    padding: 16px 32px;
  }
}

.cangra-nav .logo {
  width: 110px;
  height: 47px;
  object-fit: cover;
  position: relative;
}

.cangra-nav.scrolled {
  background-color: #294091;
  transition: all .3s linear;
}

.cangra-nav__items {
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  display: flex;
}

@media only screen and (max-width: 767px) {
  .cangra-nav__items {
    display: none;
  }
}

.cangra-nav__items .item {
  color: #fff;
  font-size: 14px;
}

.cangra-nav__items .button {
  color: #3450b5;
  background-color: gold;
  border-radius: 8px;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 10px 20px;
  font-size: 14px;
  font-weight: 500;
  display: flex;
  position: relative;
}

.cangra-nav .hamburger {
  cursor: pointer;
}

.cangra-nav .hamburger > * {
  width: 30px;
  height: 3px;
  -o-transition: all .3s ease-in-out;
  background-color: #fff;
  border-radius: 10px;
  margin: 6px 0;
  transition: all .3s ease-in-out;
  display: block;
}

.cangra-nav .hamburger .line1.active {
  transform: translateY(9px)rotate(45deg);
}

.cangra-nav .hamburger .line2.active {
  opacity: 0;
}

.cangra-nav .hamburger .line3.active {
  transform: translateY(-9px)rotate(-45deg);
}

